body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #035547;
}

.App {
  background: #2a4c45;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
}

.navbar {
  padding: 10px 20px;
  text-align: center;
  background: #3d6d66;
}

.navbar h1 {
  margin: 10px 0;
}

.book-list {
  margin: 20px;
}

.book-list ul {
  padding: 0;
  list-style-type: none;
}

.book-list li {
  background: #3d6d66;
  border-radius: 4px;
  padding: 10px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.book-list li:hover {
  opacity: 0.8;
}

.book-list .title {
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}

.book-list .author {
  font-size: 0.9em;
  color: #ddd;
}

.remove-book {
  margin-right: 20px;
  cursor: pointer;
  font-size: 1.3em;
}

.empty {
  margin: 20px;
  text-align: center;
}

form {
  padding: 20px;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #1f3c37;
  color: #fff;
  border: 0;
  border-radius: 5px;
  font-size: 0.9em;
}

input[type="text"]::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

input[type="submit"] {
  margin: 10px auto;
  background: #eee;
  border: 0;
  border-radius: 5px;
  padding: 6px 20px;
  display: block;
  font-size: 0.9em;
}
